}

.soliloquy-container .soliloquy-item:after {
    content: '.';
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1000;
    visibility: hidden;
    opacity: 0;
    height: 0;
    line-height: 0
}

.soliloquy-container .soliloquy-item > a {
